.body{
    display: flex;
    flex-direction: column;
    width: 90vw;
    background: linear-gradient(229.87deg, #BCCAE6 -14.89%, rgba(255, 255, 255, 0) 92.34%), #6386F3;
    border-radius: 20px;
    margin: auto;
    margin-top: 40px;
    margin-bottom: 40px;
    font-family: 'Raleway', sans-serif;

    @media (max-width:500px) {
        margin: 40px auto;
    }
    
    .header{
        color: #FCE4E4;
        margin-top: 100px;
        margin-left: 40vw;
        @media (max-width:1200px) {
            margin-left: 20vw;
        }
        @media (max-width:1200px) {
            margin-left: 10vw;
        }


        h1{
            font-size: 60px;
            font-style: normal;
            font-weight: normal;
            @media (max-width:800px) {
                font-size: 40px;
            }
            @media (max-width:500px) {
                font-size: 30px;
            }
        }
        h4{
            font-size: 30px;
            @media (max-width:800px) {
                font-size: 20px;
            }
            @media (max-width:50px) {
                font-size: 15px;
            }
        }
    }

    .whole{
        margin-top: 150px;
        margin-bottom: 100px;
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        flex-wrap: wrap;
        @media (max-width: 600px){
            border-top: 0px;
        }

        img{
            width:700px;
            margin-left:-100px;

            @media (max-width:1200px) {
                width:500px;
            }
            @media (max-width:1050px) {
                width:700px;
            }
            @media (max-width:800px) {
                width:420px;
            }
            @media (max-width:600px) {
                margin:0px;
                margin-top: -80px;
                margin-bottom: 50px;
                width:300px;
            }

        }

        .details{
            font-weight: lighter;
            .pink-text{
                color: #FFBDBD;
                font-size: 30px;
                @media (max-width:600px) {
                    font-size: 25px;

                }
            }
            
            p{
                font-family: 'Raleway', sans-serif;
                position: relative;
                color: #FFE9E9;                
            }
            .description{
                font-size: 24px;
                width:400px;
                @media (max-width:600px) {
                    font-size: 20px;
                    width:300px;
                    text-align: center;
                    margin-left: 20px;
                }

            }
            .first-floor{
                padding-top: 40px;
                display: flex;
                justify-content: space-between;
                .price_detailed{
                    color: #FFE9E9;
                    margin-left: 20px;
                }
                .edition{
                    width: 200px;
                    margin-right: -30px;
                    @media (max-width:600px){
                        margin-right: 0px;
                    }
                }
            }
            .second-floor{
                border-top: solid 2px  #FBFBFB;
                border-bottom: solid 2px  #FBFBFB;
                padding: 20px 0px;
                margin-top: 40px;
                display: flex;
                justify-content: space-between;
                .color{
                    margin-left: 30px;
                    margin-top:10px;
                }
                .colorP{
                    margin-right: 30px;
                }
            }
            button.btn-amazon{
                cursor: pointer;
                margin: 80px;
                margin-top: 60px;
                margin-bottom: -200px;
                width: 190px;
                height: 90px;
                @media (max-width:600px){
                    margin-bottom: 0px;
                }
            }
                    .back{
                        margin-top: -200px;
                        background-color: #222222;
                        color: #fdaa2e;
                        &:hover{
                            background-color: #2f2f2f;
                        }
                    }
        }
    }
}